class Router {
  constructor(opt) {
    const { pathArr } = opt;
    this.pathArr = pathArr;
    this.init();
    console.log('pathArr==>>', this.pathArr);
  }

  init() {
    this.bindEvent();
  }

  bindEvent() {
    window.addEventListener('hashchange', (e) => {
      console.log('e==>>', e);
    });

  }
}


export default Router;